<template>
    <el-dialog :visible.sync="visibleDialog.value" :title="title" @close="close">
        <vue-form :state="formState" @submit.prevent="onSubmit">
            <validate tag="div"
                v-for="item in fields"
                :key="item.name"
                class="m-b-lg clearfix"
                :custom="item.rules?format(item.rules):{}" v-if="judShow(item.show)">
                <el-row :gutter="30">
                    <el-col :span="6" class="text-right">
                        <label v-text="item.showName" :class="{'name-top': item.type==='read'||item.type==='switch','required': item.required}"></label>
                    </el-col>
                    <el-col :span="18">
                        <input v-model="data[item.name]" :name="item.name" :required="item.required" v-if="item.type==='text'">
                        <span v-text="data[item.name]" v-if="item.type==='read'"></span>
                        <el-switch active-color="#1385ff" v-model="data[item.name]" v-if="item.type==='switch'"></el-switch>
                        <input v-model="data[item.name]" type="password" :name="item.name" :required="item.required" v-if="item.type==='password'">
                        <textarea v-model="data[item.name]" :name="item.name" :required="item.required" v-if="item.type==='textarea'"></textarea>
                        <!-- <select :name="item.name" :required="item.required">
                            <option :value="option.value" v-for="option in item.options">
                                {{ option.text ? option.text : option.value }}
                            </option>
                        </select> -->
                        <select-area class="select-area" v-if="item.type==='select'" :name="item.name" :value="data[item.name]" :options="item.options" @getSelectValue="getSelectValue"></select-area>
                        <section v-if="item.type==='radio'">
                            <label v-for="obj in item.radioFields" :class="['radio-groups',data[item.name]===obj.value?'active':'']">
                                <span v-text="obj.name"></span>
                                <input :required="item.required" type="radio" :name="item.name" v-model="data[item.name]" :value="obj.value" @change="checkboxChange(data[item.name])">
                            </label>
                        </section>
                    </el-col>
                    <el-col :offset="6" :span="18" class="error-msg">
                        <error-msg :name="item.name" :show-msg="item.rulesMsg?item.rulesMsg:{}" :item-status="formState[item.name]"></error-msg>
                    </el-col>
                    <el-col :offset="6" :span="16" class="tip-msg" v-if="item.tip">
                        <span v-text="item.tip"></span>
                    </el-col>
                </el-row>
            </validate>
        </vue-form>
        <div class="dialog-footer">
            <el-button class="btn btn-gradient btn-sm" :disabled="formDisabled" @click="confirm">{{$t('common.confirm')}}</el-button>
            <el-button class="btn btn-transparent btn-sm" @click="close">{{$t('common.canel')}}</el-button>
        </div>
    </el-dialog>
</template>
<script>
    import errorMsg from './errorMsg'
    import { isFunction } from '@/assets/js/untils'
    import selectArea from './selectArea'
    export default {
        data() {
            return {
                formState: {}
            }
        },
        props: {
            fields: {
                type: Array,
                default: () => []
            },
            data: {
                type: Object
            },
            visible: {
                type: Boolean
            },
            title: {
                type: String
            }
        },
        components: {
            errorMsg,
            selectArea
        },
        methods: {
            confirm() {
                this.$emit('confirm', this.data)
            },
            close() {
                this.formState._reset()
                this.$emit('close')
            },
            format(rules) {
                let obj = {}
                const scopeThis = this
                for (let key in rules) {
                    if (isFunction(rules[key])) {
                        obj[key] = rules[key].call(scopeThis)
                    }
                }
                return obj
            },
            judShow(isShow) {
                const scopeThis = this
                if(isShow === undefined) {
                    return true
                }
                if(isShow !== undefined && typeof isShow === 'function') {
                    return isShow.call(scopeThis)
                }
            },
            checkboxChange(data) {
                this.$emit('checkboxChange', data)
            },
            getSelectValue(value, name) {
                this.data[name] = value
            }
        },
        computed: {
            formDisabled() {
                return this.formState.$invalid
            },
            visibleDialog() {
                return { value: this.visible }
            }
        }
    }
</script>
<style scoped>
    label {
        font-size: 14px;
        color: #696e79;
        line-height: 40px;
    }
    input {
        width: 360px;
        height: 40px;
        background-color: #ffffff;
        border-radius: 1px;
        padding: 10px;
        box-sizing: border-box;
        font-size: 14px;
        border: solid 1px #cad8df;
    }
    input:focus {
        outline: solid 1px #0c92fe;
    }
    .name-top {
        line-height: 24px;
    }
    .error-msg {
        margin-bottom: -25px;
    }
    .tip-msg {
        font-family: MicrosoftYaHei;
        font-size: 12px;
        color: #bcc5d9;
    }
    .btn-sm {
        width: 132px;
        height: 40px;
    }
    .btn-transparent {
        border-radius: 2px;
        border: solid 1px #0c92fe;
        font-size: 16px;
        color: #018fff;
    }
    .dialog-footer {
        padding-top: 20px;
        padding-right: 45px;
        text-align: right;
        border-top: #cad8df 1px solid;
    }
    textarea {
        width: 580px;
        height: 140px;
        background-color: #ffffff;
        border-radius: 1px;
        border: solid 1px #cad8df;
        resize: none;
    }
    input[type="radio"] {
        height: 0;
        width: 0;
    }
    .radio-groups {
        width: 90px;
        height: 40px;
        display: inline-block;
        border: solid 1px #1385ff;
        border-right-width: 0;
        color: #696e79;
        text-align: center;
        cursor: pointer;
    }
    .radio-groups:hover,
    .radio-groups.active {
        background-color: #1385ff;
        color: #fff;
    }
    .radio-groups:last-child {
        border-right-width: 1px;
    }
    .select-area {
        width: 360px;
        height: 40px;
    }
    </style>
<i18n>
    {
        "zh": {
            "common": {
                "confirm": "确定",
                "canel": "取消"
            }
        }
    }
</i18n>



