<template>
    <div class="table-warpper">
        <div class="pull-left">
            <slot name="btns-left"></slot>
        </div>
        <div class="pull-right">
            <slot name="btns"></slot>
        </div>
        <table-search
            @search="changeSearchText"
            v-if="searchShow"
            :searchText="searchText"
            :class="['table-search',isSearchLeft?'pull-left':'pull-right m-r-xs']"
            ></table-search>
        <el-table
            :data="showData"
            stripe
            ref="srcTable"
            @selection-change="selectChange"
            tooltip-effect="light"
            :cell-style="cellStyle"
            :header-cell-style="headerRowStyle">
            <el-table-column align="center" type="selection" v-if="selectable"></el-table-column>
            <table-item v-for="(config, index) in fields" :key="index" :config="config"></table-item>
            <el-table-column :label="operatorsTitle" :resizable="false" v-if="operators && operators.length" :width="operatorWidth">
                <template slot-scope="scope">
                    <operator
                        class="table-operate"
                        v-for="(operator, index) in operators"
                        :renderFn="operator.renderFn"
                        :scope="scope"
                        :handler="operator.handler"
                        :key="index"
                        :iconClass="operator.iconClass"
                        :name="operator.name"
                        @click="operator.handler"></operator>
                </template>
            </el-table-column>
        </el-table>
        <div :class="[isPaginationLeft?'pull-right':'pull-left']">
            <slot name="btns-bottom" :tableData="$refs.srcTable"></slot>
        </div>
        <table-pagination
            :class="['table-pagination',isPaginationLeft?'pull-left':'pull-right']"
            :pageSize="pageSize" :pagesizes="pagesizes"
            :currentPage="currentPage"
            :size="filteredData.length"
            @changePage="changePage"></table-pagination>
    </div>
</template>

<script>
    import tableSearch from './table-search.vue'
    import tablePagination from './table-pagination.vue'
    import {isArray, isFunction} from '@/assets/js/untils'

    const get = (path, obj) => {
        if (!obj) {
            return obj
        }
        path = path ? path.replace(/\[(\S+?)]/g, '.$1').replace(/^\./, '') : ''
        return path.split('.').reduce(
            (p, c) => p ? p[c] : p,
            obj
            )
    }

    const customToString = val => {
        if (typeof val === 'string') {
            return val
        }
        if (typeof val === 'number') {
        // todo: 大数字怎么办
        return val + ''
        }
        return ''
    }
// 搜索方法
const search = (fields = []) => (searchText, data) => {
    searchText = searchText.trim()
    if (searchText === '') {
        return data
    }
    return data.filter(item => {
        if (!fields.length) {
            let atts = []
            for (let att in item) {
                if (att !== '$$src') {
                    atts.push(att)
                }
            }
            fields = atts
        }
        return fields.some(field => ~customToString(item[field].value).indexOf(searchText))
    })
}

const generProps = config => {
    let props = Object.assign({resizable: false, sortable: true}, config)
    props.prop = config.name + '.vNode'
    props.label = config.showName || config.label
    delete props.showName
    delete props.path
    delete props.name
    delete props.format
    return { props }
}

export default {
    created() {
        this.search = this.initSearch()
    },
    props: {
        operators: {
            type: Array,
            default: () => []
        },
        data: {
            type: Array,
            default: () => []
        },
        fields: {
            type: Array,
            default: () => []
        },
        searchOpt: Array || Function,
        selectable: {
            type: Boolean,
            default: true
        },
        cellStyle: {
            type: Object,
            default: () => {
                return {borderBottomWidth: 0}
            }
        },
        operatorsTitle: {
            type: String,
            default: 'operate'
        },
        isSearchLeft: {
            type: Boolean,
            default: true
        },
        isPaginationLeft: {
            type: Boolean,
            default: false
        },
        searchShow : {
            type: Boolean,
            default: true
        },
        headerRowStyle: {
            type: Object,
            default: () => {
                return {backgroundColor: '#f5f7f8'}
            }
        },
        operatorWidth: {
            type: Number
        }
    },
    components: {
        tableSearch,
        tablePagination,
        tableItem: {
            props: {
                config: Object
            },
            render() {
                return (<elTableColumn {...generProps(this.config)}></elTableColumn>)
            }
        },
        operator: {
            props: {
                name: String,
                title: String,
                handler: Function,
                scope: Object,
                iconClass: String,
                renderFn: Function
            },
            render() {
                if (this.renderFn) {
                    let result = this.renderFn(this.scope.row)
                    let isVnode = result.hasOwnProperty('componentOptions')
                    if (this.handler && isVnode && (!result.data || !result.data.on || !result.data.on.click)) {
                        result.data = result.data || {}
                        result.data.on
                        ? result.data.on.click = e => this.handler(this.scope.row, e)
                        : result.data.on = {click: e => this.handler(this.scope.row, e)}
                    }
                    return result
                }
                let content = this.iconClass
                ? <i class={this.iconClass}></i>
                : this.name

                return (<a class="operator" title={this.name} onClick={e => this.handler(this.scope.row, e)}>
                    {content}
                </a>)
            }
        }
    },
    methods: {
        selectChange(selectRows) {
            this.$emit('selection-change', selectRows)
        },
        changePage(pagetaion) {
            this.currentPage = pagetaion.currentPage
            this.pageSize = pagetaion.pageSize
        },
        changeSearchText(st) {
            this.searchText = st
        },
        initSearch() {
            if (isFunction(this.searchOpt)) {
                return this.searchOpt
            }
            if (isArray(this.searchOpt)) {
                return search(this.searchOpt)
            }
            return search()
        },
        reset() {
            if (this.data && this.data.length) {
                this.currentPage = 1
            } else {
                this.currentPage = 0
            }
            this.searchText = ''
            this.pageSize = this.pagesizes[0]
        }
    },
    data() {
        return {
            currentPage: 1,
            searchText: '',
            pagesizes: [10, 20, 50],
            pageSize: 10,
            search: (t, d) => d
        }
    },
    computed: {
        filteredData() {
            return this.search(this.searchText, this.convertData)
        },
        showData() {
            let start = (this.currentPage - 1) * this.pageSize
            return this.filteredData.slice(start, start + this.pageSize)
        },
        convertData() {
            if (!isArray(this.data)) {
                return []
            }
            return this.data.map(item => {
                let result = { $$src: item }
                this.fields.forEach(fieldConifg => {
                    let value = get(fieldConifg.path || fieldConifg.name, item)
                    value = isFunction(fieldConifg.format) ? fieldConifg.format(value, item) : value
                    result[fieldConifg.name] = {
                        value: value,
                        vNode: isFunction(fieldConifg.formatToVnode) ? fieldConifg.formatToVnode(value, item) : value
                    }
                })
                return result
            })
        }
    },
    watch: {
        data(newData, oldData) {
            this.reset()
        }
    }
}
</script>

<style scoped>
    .operator:hover {
        opacity: 1;
    }

    .operator {
        opacity: .9;
        font-size: 14px;
        cursor: pointer;
        margin-right: 18px;
    }

    .operator:last-child {
        margin-right: 0;
    }

    .table-warpper {
        padding: 20px;
    }

    .el-table .el-table-column--selection {
        text-align: center;
    }

    .el-table::before {
        content: none;
    }

    .table-search input {
        height: 30px;
    }

    .table-search .el-input__icon {
        line-height: 30px;
    }

    .table-operate {
        color: #22a5df;
    }

    .table-search {
        width: 280px;
        margin-bottom: 40px;
    }

    .table-pagination {
        padding: 20px 0;
    }
</style>



