<template>
    <div class="el-pagination bg-white text-right table-pagination">
        <span class="el-pagination__total">{{info}}</span>
        <button type="button" class="btn-prev" @click="changePage('first')" :disabled="currentPage === firstpage">
            <i class="el-icon el-icon-d-arrow-left"></i>
        </button>
        <button type="button" class="btn-prev" @click="changePage('pre')" :disabled="currentPage === firstpage">
            <i class="el-icon el-icon-arrow-left"></i>
        </button>

        <span>
            <el-dropdown trigger="click" placement="bottom-start" @command="changePage">
                <span class="el-dropdown-link">{{currentPage}}<i class="el-icon-arrow-down"></i></span><span>/{{totalPage}}</span>
                <el-dropdown-menu slot="dropdown" class="dropdown-page" :class="[totalPage.length ? '' : 'no-border']">
                    <el-dropdown-item v-for="item in totalPage" :key="item" :command="item">{{item}}</el-dropdown-item>
                </el-dropdown-menu>
            </el-dropdown>
        </span>

        <button type="button" class="btn-next" @click="changePage('next')" :disabled="currentPage === totalPage">
            <i class="el-icon el-icon-arrow-right"></i>
        </button>
        <button type="button" class="btn-next" @click="changePage('last')" :disabled="currentPage === totalPage">
            <i class="el-icon el-icon-d-arrow-right"></i>
        </button>

        <span class="el-pagination__sizes">
            <el-select :value="pageSize" @input="onInput">
                <el-option v-for="item in pagesizes" :value="item" :key="item" :label="item + $t('pagination.pagesize')"></el-option>
            </el-select>
        </span>
    </div>
</template>

<script>
    export default {
        props: {
            // 当前每页显示条数
            pageSize: {
                type: Number,
                default: 10
            },
            // 当前页
            currentPage: {
                type: Number,
                default: 0
            },
            // 总数据量
            size: {
                type: Number,
                default: 0
            },
            pagesizes: {
                type: Array,
                default: () => [10, 20, 50]
            }
        },
        methods: {
            onInput(val) {
                this.emitChange({pageSize: val})
            },
            changePage(stategy) {
                let currentPage
                switch (stategy) {
                    case 'first':
                        currentPage = this.firstpage
                        break
                    case 'last':
                        currentPage = this.totalPage
                        break
                    case 'pre':
                        currentPage = this.currentPage - 1
                        break
                    case 'next':
                        currentPage = this.currentPage + 1
                        break
                    default:
                        currentPage = stategy
                }
                this.emitChange({currentPage})
            },
            emitChange(pagetaion) {
                pagetaion = Object.assign({
                    currentPage: this.currentPage,
                    pageSize: this.pageSize
                }, pagetaion)
                this.$emit('changePage', pagetaion)
            }
        },
        computed: {
            firstpage() {
                if (this.size) {
                    return 1
                }
                return 0
            },
            totalPage() {
                return Math.ceil(this.size / this.pageSize)
            },
            info() {
                let first = this.size === 0 ? 0 : (this.currentPage - 1) * this.pageSize + 1
                let end = (this.currentPage - 1) * this.pageSize + this.pageSize
                if (end > this.size) {
                    end = this.size
                }
                return this.$t('pagination.pageinfo', {first: first, end: end, size: this.size})
            }
        }
    }
</script>

<style>
    .dropdown-page.el-dropdown-menu.el-popper {
        max-height: 170px;
        overflow-y: auto;
        overflow-x: hidden;
        text-align: center;
        margin-top: 0;
        width: 50px;
    }

    .dropdown-page li {
        padding: 0 15px;
    }

    .el-pagination .el-select .el-input {
        width: 55px;
    }

    .el-pagination button, .el-pagination span:not([class*=suffix]) {
        min-width: 15px;
    }

    .table-pagination .el-pagination__sizes {
        margin-right: 0;
    }

    .no-border {
        border: none;
    }

    .el-popper .popper__arrow {
        display: none;
    }
</style>

<i18n>
    {
        "zh": {
            "pagination": {
                "pagesize": " ",
                "pageinfo": "第{first}-{end}条，总计{size}条"
            }
        }
    }
</i18n>



