<template>
    <el-input :placeholder="$t(placeholder)" :value="searchText" :suffixIcon="suffixIcon" @input="input"></el-input>
</template>

<script>
    let timeoutId
    export default {
        props: {
            searchText: {
                type: String,
                default: ''
            },
            placeholder: {
                type: String,
                default: 'input.for.search'
            },
            suffixIcon: {
                type: String,
                default: 'el-icon-search'
            },
            delay: {
                type: Number,
                default: 500
            }
        },
        methods: {
            input(value) {
                if (typeof timeoutId === 'number') {
                    window.clearTimeout(timeoutId)
                }
                timeoutId = setTimeout(
                    () => this.$emit('search', value),
                    this.delay
                )
            }
        }
    }
</script>

<style>

</style>

<i18n>
    {
        "zh": {
            "input.for.search": "请输入搜索"
        }
    }
</i18n>



