<template>
    <el-dialog :visible.sync="visibleDialog.value" @close="close" :title="title" :class="dialogClass">
        <div class="dialog-body" v-if="message">
            {{message}}
        </div>
        <div class="dialog-body clearfix" v-if="useTemplate">
            <slot name="body"></slot>
        </div>
        <div class="dialog-footer" v-if="type === 'tip'">
            <el-button class="btn btn-gradient btn-sm" @click="close">{{$t('common.confirm')}}</el-button>
        </div>
        <div class="dialog-footer" v-else>
            <el-button class="btn btn-gradient btn-sm"  @click="confirm">{{$t('common.confirm')}}</el-button>
            <el-button class="btn btn-transparent btn-sm" @click="close">{{$t('common.canel')}}</el-button>
        </div>
    </el-dialog>
</template>
<script>
    export default {
        data() {
            return{

            }
        },
        props: {
            type: {
                type: String,
                default: ""
            },
            visible: {
                type: Boolean
            },
            title: {
                type: String
            },
            message: {
                type: String
            },
            useTemplate: {
                type: String,
                default: 'false'
            },
            dialogClass: {
                type: String
            }
        },
        methods: {
            close() {
                this.$emit('close')
            },
            confirm() {
                this.$emit('confirm')
            }
        },
        computed: {
            visibleDialog() {
                return { value: this.visible }
            }
        }
    }
</script>
<style scoped>
    .dialog-body {
        margin-bottom: 20px;
        padding-left: 25px;
    }
    .dialog-footer {
        padding-top: 20px;
        padding-right: 45px;
        text-align: right;
        border-top: #cad8df 1px solid;
    }
    .btn-sm {
        width: 132px;
        height: 40px;
    }
</style>
<i18n>
    {
        "zh": {
            "common": {
                "confirm": "确定",
                "canel": "取消"
            }
        } 
    }
</i18n>


